@import "responsive";

.main__sidebar {
  background: #0c0f26;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  width: 20%;
  float: left;
  height: 100%;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  font-size: 1.6rem;

  @include respond(tab-land) {
    float: none;
    width: 100%;
    text-align: center;
  }
}

.container-fluid {
  padding-left: 0 !important;
  padding-right: 0 !important;
}
.main {
  height: auto;
  background: #0c0f26;
}
.main__video__container {
  background: #171934;
  height: 100%;
  width: auto;
  padding: 3rem 3rem 15rem 3rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  min-height: 100vh;

  .load__more {
    padding: 2rem;
    background: #7678fb;
    color: white;
    font-weight: 700;
    text-align: center;
    position: absolute;
    bottom: 0;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
    -ms-transform: translate(-50%, 0);
    transform: translate(-50%, 0);
    text-transform: uppercase;
    display: inline-block;
  }

  @include respond(phone) {
    padding: 3rem 0;
  }
}

a.sidebar__link {
  display: block;
  padding: 2rem;
  font-size: 1.3rem;
  background: #0c0f26;
  width: 100%;
  color: #7678fb;
  text-transform: uppercase;

  &:hover {
    background: #0f0f31;
  }
}

.game__card {
  height: 40rem;
  margin: 1rem;
  width: 100%;
  cursor: pointer;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  overflow: hidden;
  border-radius: 10px;

  @include respond(phone) {
    width: 100%;
  }
}

.game__card__top {
  height: 80%;
  -webkit-transition: -webkit-transform 0.3s ease-in-out;
  transition: -webkit-transform 0.3s ease-in-out;
  -o-transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
  &:hover {
    -webkit-transform: scale(1.02, 1.02);
    -ms-transform: scale(1.02, 1.02);
    transform: scale(1.02, 1.02);
  }
  img {
    -o-object-fit: fill;
    object-fit: fill;
    -o-object-position: center;
    object-position: center;
    width: 100%;
    height: 100%;

    @include respond(phone) {
      -o-object-fit: contain;
      object-fit: contain;
    }
  }
}

.game__card__bottom {
  height: 20%;
  color: white;
  max-width: 100%;
  padding-top: 1rem;
  overflow: hidden;

  @include respond(phone) {
    text-align: center;
  }

  em {
    font-weight: 700;
  }
  p {
    font-weight: 700;
    color: #737783;
    line-height: 2.5;
    font-size: 1.7rem;
  }

  i {
    margin-right: 5px;
  }
}

.pagination {
  position: absolute;
  bottom: 0;
  left: 50%;
  -webkit-transform: translate(-50%);
  -ms-transform: translate(-50%);
  transform: translate(-50%);
}

.stream__card {
  background: #171934;
  height: 30rem;
  width: 100%;
  margin: 1rem;
  cursor: pointer;
}

.stream__card__top {
  width: 100%;
  height: 80%;
  position: relative;

  img {
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
    -o-object-position: center;
    object-position: center;
  }
}

.stream__card__bottom {
  width: 100%;
  height: 20%;
  padding-top: 1rem;
  img {
    -o-object-fit: contain;
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}

.stream__card__right {
  width: 90%;
  height: 100%;
  float: right;
  padding: 0.5rem;
}

.stream__card__left {
  width: 10%;
  height: 100%;
  float: left;
}

.pagination-lg > li > a,
.pagination-lg > li > span {
  background: #171934;
  color: #7678fb;
  border: none;
}

.stream__status {
  color: white;
  background: red;
  padding: 0.5rem 1rem;
  display: inline-block;
  position: absolute;
  top: 5%;
  left: 5%;
  border: none;
  text-transform: uppercase;
}

.stream__viewers {
  color: white;
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  display: inline-block;
  bottom: 5%;
  left: 5%;
  border: none;
}
